import { useEffect } from "react";
import styled from "styled-components";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { auth, provider } from "../firebase";
import {
  selectUserName,
  selectUserPhoto,
  setUserLoginDetails,
  setSignOutState,
} from "../features/user/userSlice";

const Login = (props) => {


    const dispatch = useDispatch();
    const history = useHistory();
    const userName = useSelector(selectUserName);
    const userPhoto = useSelector(selectUserPhoto);
  
    useEffect(() => {
      auth.onAuthStateChanged(async (user) => {
        if (user) {
          setUser(user);
          history.push("/home");
        }
      });
    }, [userName]);
  
    const handleAuth = () => {
      if (!userName) {
        auth
          .signInWithPopup(provider)
          .then((result) => {
            setUser(result.user);
          })
          .catch((error) => {
            alert(error.message);
          });
      } else if (userName) {
        auth
          .signOut()
          .then(() => {
            dispatch(setSignOutState());
            history.push("/");
          })
          .catch((err) => alert(err.message));
      }
    };
  
    const setUser = (user) => {
      dispatch(
        setUserLoginDetails({
          name: user.displayName,
          email: user.email,
          photo: user.photoURL,
        })
      );
    };


    return (
        <Container>
            <Content>
                <CTA>
                    <CTALogoOne src="/images/cta-logo-one.svg" alt='' />
                    <SignUp onClick={handleAuth}>GET THE DISNEY BUNDLE</SignUp>
                    <Description>
                        Get Premier Access to all Star Wars movies for an 
                        additional fee with a Disney+ subscription. As of 02/26/22, 
                        the price of Disney+ and The Disney Bundle will increase by $5.
                    </Description>
                    <CTALogoTwo src='/images/cta-logo-two.png' alt='' />
                </CTA>
                <BackgroundImage />
            </Content>
        </Container>
    );
};

const Container = styled.section`
overflow: hidden;
display:flex;
flex-direction:column;
text-align:center;
height: 100%;
`;


const Content = styled.div`
margin:0 0 10px 0;
width:100%;
position: relative;
min-height: 100vh;
box-sizing: border-box;
display: flex;
justify-content:center;
align-items:center;
flex-direction:column;
padding: 80px 40px;
height: 100%;
`;

const BackgroundImage = styled.div`
height: 100%;
background-position: top;
background-size: cover;
background-repeat: no-repeat;
background-image: url('/images/login-background.jpg');
position: absolute;
top: 0;
right: 0;
left:0;
z-index: -1;
`;

const CTA = styled.div`
margin: 0 auto 2vw auto;
max-width:550px;
flex-wrap: wrap;
display: flex;
flex-direction: column;
width: 100%;
`;

const CTALogoOne = styled.img`
margin: 0 0 12px 0;
max-width:600px;
min-height:1px;
display: block;
width: 100%;
`;

const SignUp = styled.a`
color: #f9f9f9;
background-color: #0063e5;
margin: 0 0 20px 0;
width: 100%;
letter-spacing: 1.5px;
font-size: 18px;
padding: 15.5px 0;
border: 1px solid transparent;
border-radius: 4px;
transition: all 0.2s ease 0s;

&:hover {
    background-color: #0483ee;
    cursor: pointer;
}
`;

const Description = styled.p`
color: hsla(0, 0%, 95.3%, 1);
font-size: 13.5px;
margin: 0 0 24px;
line-height: 1.5;
letter-spacing: 1.5px;
`;

const CTALogoTwo = styled.img`
max-width: 600px;
margin: 0 0 20px 0;
display: inline-block;
vertical-align: bottom;
width: 100%;
`;

export default Login;